<template>
  <div>
    <v-toolbar
    app
    dark
    color="primary"
    >
      <v-toolbar-side-icon @click="toogleDrawer"/>
      <v-toolbar-title>
        <v-btn color="dark" to="/">
          vue firestore
        </v-btn>
      </v-toolbar-title>
      <v-spacer/>
      <v-toolbar-items class="hidden-sm-and-down">
          <v-btn flat to="/orders">{{ $t('navigation.orders')}}</v-btn>
          <v-btn flat to="/cart">{{$t('navigation.products_in_cart', {number: $store.state.cartModule.totalProducts })}}</v-btn>
          <v-btn flat to="/shop">{{$t('navigation.shop')}}</v-btn>
          <v-btn @click="logout" flat to="/login">{{$t('navigation.logout')}} </v-btn>
        </v-toolbar-items>
      </v-toolbar>

      <v-navigation-drawer
        overlay
        v-model="drawer"
        absolute
        value="">
        <v-list class="pt-0" dense>
          <v-divider />
          <v-list-tile v-for="(item, index) in items" :key="item.title" @click="$router.push(item.to)">

            <v-list-tile-action :key="index">
              <v-icon>{{item.icon}}</v-icon>
             </v-list-tile-action>
             <v-list-tile-content>
               <v-list-tile-title>{{ item.title}}</v-list-tile-title>
             </v-list-tile-content>
            </v-list-tile>

          <v-list-tile  @click="toogleDrawer">
            <v-list-tile-action >
              <v-icon>arrow_back</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title @click="logout">{{ $t('navigation.close_menu')}} </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
        <v-divider/>
        <!-- <v-list-tile @click="logout">
          <v-list-tile-action>
            <v-icon>forward</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>
              {{$t('navigation.logout')}}
            </v-list-tile-title>
          </v-list-tile-content> -->
        </v-list-tile>
      </v-navigation-drawer>

  </div>
</template>
<script>
import NavigationMixin from '@/mixins/navigation'
export default {
  name: 'Customer',
  mixins: [NavigationMixin],
  data () {
    return {
      drawer: false,
      items: [
        { title: this.$t("navigation.home"), icon: "home", to: "/admin" },
        { title: this.$t("navigation.shop"), icon: "shopping_basket", to: "/shop"},
        {title: this.$t("navigation.cart"), icon: "shopping_cart", to: "/cart"},
        {title: this.$t("navigation.orders"), icon: "view_headline", to: "/orders"
        }
      ]
    }
  }
}
</script>



